@import url("https://fonts.googleapis.com/css2?family=Neucha&display=swap");

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #2f74c0;
  font-family: "Neucha", cursive;
}

.container {
  width: 90%;
  display: flex;
  flex-direction: column;
  max-width: 1140px;
  /* width: 95%; */
  margin-top: 10px;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.gap-2 {
  gap: 1rem;
}

.mt-2 {
  margin-top: 1rem;
}

.flex {
  display: flex;
}

.w-full {
  width: 100%;
}

.heading {
  text-transform: uppercase;
  font-size: 2.5rem;
  margin: 2rem 0;
  color: white;
  z-index: 1;
  text-align: center;
}

@media (max-width: 800px) {
  .heading {
    margin: 1rem 0;
    font-size: 2rem;
  }
  .flex {
    flex-direction: column;
  }
}
